
'use client';

import { useEffect } from 'react';
import { useRouter } from 'next/navigation';
import { useAuth } from '@/contexts/AuthContext';
import { Skeleton } from '@/components/ui/skeleton';

export default function HomePage() {
  const { user, isLoading } = useAuth();
  const router = useRouter();

  useEffect(() => {
    if (!isLoading) {
      if (user) {
        router.replace('/dashboard');
      } else {
        router.replace('/login');
      }
    }
  }, [user, isLoading, router]);

  return (
    <div className="flex h-screen flex-col items-center justify-center space-y-4 bg-background p-8">
      <svg className="h-16 w-16 text-primary" viewBox="0 0 24 24" fill="currentColor" xmlns="http://www.w3.org/2000/svg">
        <path d="M12 2L1 9L12 16L23 9L12 2ZM5 10.5V16.5L12 20.5L19 16.5V10.5L12 14L5 10.5Z" />
      </svg>
      <h1 className="text-3xl font-headline text-primary">Campus Hub</h1>
      <p className="text-muted-foreground">Loading your personalized experience...</p>
      <div className="w-full max-w-md space-y-3 pt-4">
        <Skeleton className="h-8 w-full" />
        <Skeleton className="h-8 w-3/4" />
        <Skeleton className="h-8 w-1/2" />
      </div>
    </div>
  );
}
